/* Type Sizes From https://type-scale.com/ */
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 0.9em;
}

body {
  background-color: $white;
  font-family: Helvetica, 'montserrat', sans-serif;
  line-height: 1.45;
  color: #333;
}

p {
  margin-bottom: 1.3em;
  line-height: 2.5;
}

h1,
h2,
h3,
h4 {
  margin: 1.414em 0 0.5em;
  font-weight: inherit;
  line-height: 1.2;
}

h1 {
  margin-top: 0;
  // 63px
  font-size: 3.998em;
}

h2 {
  // 45px
  font-size: 2.827em;
}

h3 {
  // 31px
  font-size: 1.999em;
}

h4 {
  // 22px
  font-size: 1.414em;
}

small,
.font_small {
  // 11px
  font-size: 0.707em;
}

hr {
  margin: 3em 0;
  border-top: 1px solid rgb(230, 230, 230);
}

.container {
  width: 95%;
  margin: 0 auto;
  padding: 0 1em;

  @include center-content;
}
